<template>
  <div id="indexPool">
    
    <div class="pool-block qq-block--cont pool-block--bg">
      <div class="pool-block--title">
        流动性挖矿
      </div>
      <div class="flex-between cell-balance">
        <div class="info-item flex-item">
          <p class="tit">流动性挖矿总量(wind token)</p>
          <p class="num big-num">{{totalMarketWind}}</p>
        </div>
      </div>
      <div class="flex-between cell-balance mt12">
        <div class="info-item flex-item">
          <p class="tit">流动性挖矿余额(BUSD)</p>
          <p class="num c-1FC7D4">{{marketToTalMoney}}</p>
        </div>
        <div class="info-item flex-item">
          <p class="tit">流动性挖矿损益(BUSD)</p>
          <p class="num c-2FBBA1">{{Number(curCycleIncome - marketToTalMoney).toFixed(2)}}</p>
        </div>
      </div>
      <!-- <div class="cell-tit">委托数量</div>
      <van-field
        type="number"
        v-model="amount"
        placeholder="请输入您的委托数量">
        <template #extra><span class="input-right">BUSD</span></template>
      </van-field> -->
      <!-- <div class="cell-button">
      </div> -->
      <div class="cell-button flex">
        <van-button type="primary" size="large" color="#8F80BA" @click="showCyModal = true">冻结</van-button>
        <van-button type="primary" size="large" @click="showModal = true">赎回</van-button>
      </div>
      <!-- 做市商累计分红 -->
      <div class="flex-between cell-balance border-1px-t">
        <div class="info-item flex-item">
          <p class="tit">我的冻结：</p>
          <p class="num big-num">{{userMarket}} BUSD</p>
        </div>
      </div>
      <div class="flex-middle pool-block--link mt12">
        <div class="flex-item">
          <router-link class="c-1FC7D4" :to="{name: 'ListPool'}">
            待领取金额记录>>
          </router-link>
        </div>
        <div class="btn" v-if="Number(marketIncome) > 0 && !isGet" @click="receiveMarketIncome">领取</div>
        <div class="btn" v-else-if="Number(userMarket) > 0 && isGet">已领取</div>
      </div>
    </div>
    <div class="pool-block qq-block--cont">
      <div class="pool-block--title">
        锁仓分红
      </div>
      <div class="flex-between cell-balance">
        <div class="info-item flex-item">
          <p class="tit">总冻结(wind token)</p>
          <p class="num c-2FBBA1">{{freezeTokenNum}}</p>
        </div>
        <div class="info-item flex-item">
          <p class="tit flex-middle icon-box">我的冻结(wind token) <router-link class="icon-right" :to="{name: 'ListFreeze'}">
          <img src="@/assets/images/record@2x.png" alt="" :srcset="`${require('@/assets/images/record@3x.png')} 3x`" class="icon-record">
        </router-link></p>
          <p class="num c-D4566C">{{userBlance}}</p>
        </div>
      </div>
      <div class="cell-button flex">
        <van-button type="primary" size="large" color="#8F80BA" @click="showDjModal = true">冻结</van-button>
        <van-button type="primary" size="large" @click="showShModal = true">解冻</van-button>
      </div>
      <div class="flex-between cell-balance border-1px-t">
        <div class="info-item flex-item">
          <p class="tit fs12">锁仓累积分红(BUSD)</p>
          <p class="num c-1FC7D4">{{totalPool}}</p>
        </div>
        <!-- <div class="info-item flex-item">
          <p class="tit fs12">挖矿(已挖出wind token)</p>
          <p class="num c-1FC7D4">{{totalMarketWind}}</p>
        </div> -->
      </div>
      <div class="flex-between cell-balance mt12">
        <div class="info-item flex-item">
          <p class="tit fs12">上轮分红总额(BUSD)</p>
          <p class="num c-1FC7D4">{{yesPool}}</p>
        </div>
      </div>
      <div class="pool-block--tip">我的待领取收益：{{freezeIncome}} BUSD</div>
      <div class="flex-middle pool-block--link">
        <div class="flex-item">
          <router-link class="c-1FC7D4" :to="{name: 'FrozenPool'}">
            领取记录>>
          </router-link>
        </div>
        <div class="btn" v-if="Number(freezeIncome)" @click="getReward">领取</div>
      </div>
    </div>
    
    <!-- 冻结天数 -->
    <van-popup v-model:show="showDayPicker" position="bottom">
      <van-picker
        :columns="day"
        @confirm="onDayConfirm"
        @cancel="showDayPicker = false"
      />
    </van-popup>
    <!-- 流动性挖矿的冻结 -->
    <van-popup v-model:show="showCyModal" :style="{ background: 'transparent' }">
      <div class="modal-box">
        <div class="modal-title">冻结</div>
        <div class="modal-close flex-center" @click="showCyModal = false"><img src="@/assets/images/close@2x.png" alt="" :srcset="`${require('@/assets/images/close@3x.png')} 3x`" class="icon_arrow"></div>
        <div class="modal-cont">
          <van-field
            type="number"
            v-model="amount"
            placeholder="请输入您的委托数量">
            <template #extra><span class="input-right">BUSD</span></template>
          </van-field>
        </div>
        <div class="modal-btn">
          <van-button type="primary" size="large" v-if="!address" @click="initWallet">连接钱包</van-button>
          <van-button type="primary" size="large" v-else-if="!Number(tokenAllowance)" @click="approve">授权BUSD</van-button>
          <van-button type="primary" :block="true" size="large" v-else @click="addpoolMoney">确定</van-button>
        </div>
      </div>
    </van-popup>
    <!-- 赎回 -->
    <van-popup v-model:show="showModal" :style="{ background: 'transparent' }">
      <div class="modal-box">
        <div class="modal-title">赎回</div>
        <div class="modal-close flex-center" @click="showModal = false"><img src="@/assets/images/close@2x.png" alt="" :srcset="`${require('@/assets/images/close@3x.png')} 3x`" class="icon_arrow"></div>
        <div class="modal-cont">
          <div class="modal-tip flex-between">
            <div>赎回数量</div>
            <div class="c-1FC7D4">上限：{{userMarket}}</div>
          </div>
          <van-field
            type="number"
            v-model="wAmount"
            placeholder="请输入赎回BUSD token">
          </van-field>
        </div>
        <div class="modal-btn">
          <van-button type="primary" size="large" v-if="!address" @click="initWallet">连接钱包</van-button>
          <van-button type="primary" size="large" v-else @click="markerRedeem">确定</van-button>
        </div>
      </div>
    </van-popup>
    <!-- 冻结 -->
    <van-popup v-model:show="showDjModal" :style="{ background: 'transparent' }">
      <div class="modal-box">
        <div class="modal-title">冻结wind token</div>
        <div class="modal-close flex-center" @click="showDjModal = false"><img src="@/assets/images/close@2x.png" alt="" :srcset="`${require('@/assets/images/close@3x.png')} 3x`" class="icon_arrow"></div>
        <div class="modal-cont">
          <div class="modal-tip flex-between">
            <div>余额</div>
            <div class="c-1FC7D4">{{userWindBlance}}wind token</div>
          </div>
          <van-field
            type="number"
            v-model="windAmount"
            placeholder="请输入冻结wind token的数量">
          </van-field>
        </div>
        <div class="modal-btn">
          <van-button type="primary" size="large" v-if="!address" @click="initWallet">连接钱包</van-button>
          <van-button type="primary" size="large" v-else-if="!Number(windAllowance)" @click="approveWind">授权Wind Token</van-button>
          <van-button type="primary" size="large" v-else @click="freezeToken">确定</van-button>
        </div>
      </div>
    </van-popup>
     <van-popup v-model:show="showShModal" :style="{ background: 'transparent' }">
      <div class="modal-box">
        <div class="modal-title">解冻</div>
        <div class="modal-close flex-center" @click="showShModal = false"><img src="@/assets/images/close@2x.png" alt="" :srcset="`${require('@/assets/images/close@3x.png')} 3x`" class="icon_arrow"></div>
        <div class="modal-cont">
          <div class="modal-tip flex-between">
            <div>解冻数量</div>
            <div class="c-1FC7D4">上限：{{userBlance}}</div>
          </div>
          <van-field
            type="number"
            v-model="shAmount"
            placeholder="请输入解冻wind token">
          </van-field>
        </div>
        <div class="modal-btn">
          <van-button type="primary" size="large" v-if="!address" @click="initWallet">连接钱包</van-button>
          <van-button type="primary" size="large" v-else @click="withdrawFreezeToken">确定</van-button>
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { Toast } from 'vant';
import { reactive, toRefs, computed, watch} from "vue";
import BSNAbi from "@/utils/BSNAbi";
import tokenAbi from "@/utils/abi";
import BSNDataAbi from "@/utils/BSNDataAbi";
import freezeAbi from "@/utils/freezeAbi";
import { MaxUint256, AddressZero } from "@ethersproject/constants";
import config from "@/utils/config";
import web3Data from "@/utils/initWeb3";
import {useStore} from "vuex";
import Bignumber from "bignumber.js";
export default {
  setup() {
    const store = useStore()
    const data = reactive({
      day: [
        '3天',
        '4天'
      ],
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      BsnContract: null,
      amount: '',
      windAmount: '',
      BusdContract: null,
      freezeContract: null,
      windTokenContract: null,
      BsnDataContract: null,
      freezeTokenNum: 0,
      windAllowance: 0,
      tokenAllowance: 0,
      marketToTalMoney: 0,
      userMarket: 0,
      wAmount: '',
      curCycleIncome: 0,
      showDjModal: false,
      showCyModal: false,
      showModal: false,
      showDayPicker: false,
      showShModal: false,
      shAmount: '',
      marketIncome: 0,
      isGet: false,
      freezeIncome: 0,
      totalPool: 0,
      userBlance: 0,
      totalMarketWind: 0,
      userWindBlance: 0,
      yesPool: 0,
      dayValue: '3天',
      onDayConfirm: (value) => {
        data.dayValue = value;
        data.showDayPicker = false;
      },
      initWallet: () => {
         web3Data.initWeb3()
      },
      initContract: () => {
        data.BsnContract = new data.web3.eth.Contract(BSNAbi, config.BsnAddress)
        data.BusdContract = new data.web3.eth.Contract(tokenAbi, config.BusdAddress)
        data.BsnDataContract = new data.web3.eth.Contract(BSNDataAbi, config.BsnDataAddress)
        data.freezeContract = new data.web3.eth.Contract(freezeAbi, config.frozen)
        data.windTokenContract = new data.web3.eth.Contract(tokenAbi, config.windToken)
        data.initData()
      },
       initData: () => {
         data.getCurCycleIncome()
         data.getUserInfo()
         data.getTokenAllowance()
         data.getFreezeToken()
         data.getMarketIncome()
         data.getTotalData()
         data.getFreezeIncome()
      },
      getTotalData: async () => {
        let decimals = await data.BusdContract.methods.decimals().call();
        let decimalsWind = await data.windTokenContract.methods.decimals().call();
        let result = await Promise.all([data.BsnDataContract.methods.totalPool().call(),
           data.BsnDataContract.methods.totalMarketWind().call(),
           data.windTokenContract.methods.balanceOf(data.address).call()
         ]);
        data.totalPool = new Bignumber(result[0]).dividedToIntegerBy(Math.pow(10, decimals)).toFixed(3)
        data.totalMarketWind =  new Bignumber(result[1]).dividedToIntegerBy(Math.pow(10, decimalsWind)).toFixed(3)
        data.userWindBlance = new Bignumber(result[2]).dividedToIntegerBy(Math.pow(10, decimalsWind)).toFixed(3)
      },
      getCurCycleIncome: async () => {
        let decimals = await data.BusdContract.methods.decimals().call();
        let cur =  await data.BsnContract.methods.getCurCycleIncome().call();
        let dayNum = await data.BsnDataContract.methods.getCurDayNum().call()
        let yesMarketToTalMoney = await data.BsnDataContract.methods.getCurCycleData(dayNum - 1).call();
        data.curCycleIncome = Number(cur.curAmount / Math.pow(10, decimals)).toFixed(2)
        data.marketToTalMoney = Number(cur.curDayMarketTotal / Math.pow(10, decimals)).toFixed(2);
        data.yesPool = Number(yesMarketToTalMoney.poolAmount / Math.pow(10, decimals)).toFixed(2)
        console.log('yesMarketToTalMoney', yesMarketToTalMoney)
      },
      getUserInfo: async () => {
        let  userInfo = await data.BsnDataContract.methods.getUserInfo(data.address).call();
        let decimals = await data.BusdContract.methods.decimals().call();
        let proportion = await data.BsnDataContract.methods.getProportion().call();
        data.userMarket = Number(userInfo.marketProvide * proportion[0]/proportion[1]/ Math.pow(10, decimals)).toFixed(2);
      },
      addpoolMoney: async () => {
        if (data.amount < 1000) {
          Toast('数量必须大于等于1000')
          return
        }
        let decimals = await data.BusdContract.methods.decimals().call();
        let amount = new Bignumber(data.amount).multipliedBy(Math.pow(10, decimals)).toString();
        let balance = await data.BusdContract.methods.balanceOf(data.address).call();
        if (amount > balance) {
          Toast('余额不足')
          return
        }
        let gasPrice = await data.web3.eth.getGasPrice()
         await data.BsnContract.methods.addMarketPool(amount, AddressZero).send({from: data.address, gasPrice: gasPrice});
         Toast('参与成功')
         data.initData()
      },
      approveWind: async () => {
        let gasPrice = await data.web3.eth.getGasPrice()
        await data.windTokenContract.methods.approve(config.frozen, MaxUint256).send({from: data.address, gasPrice: gasPrice});
        data.getTokenAllowance() 
      },
      approve: async () => {
        let gasPrice = await data.web3.eth.getGasPrice()
         await data.BusdContract.methods.approve(config.BsnAddress, MaxUint256).send({from: data.address, gasPrice: gasPrice});
        data.getTokenAllowance()
      },
      getTokenAllowance: async () => {
        let result = await data.BusdContract.methods.allowance(data.address, config.BsnAddress).call();
        let wResult = await data.windTokenContract.methods.allowance(data.address, config.frozen).call();
        data.tokenAllowance = result;
        data.windAllowance = wResult;
      },
      markerRedeem: async () => {
        if (data.wAmount <= 0) {
          Toast('数量必须大于0')
          return
        }
        data.showModal = false;
        let decimals = await data.BusdContract.methods.decimals().call();
        let amount = new Bignumber(data.wAmount).multipliedBy(Math.pow(10, decimals)).toString();
        let gasPrice = await data.web3.eth.getGasPrice()
        await data.BsnContract.methods.markerRedeem(amount).send({from: data.address, gasPrice: gasPrice});
        Toast('赎回成功')
        data.initData()
      },
      freezeToken: async () => {
        if (data.windAmount <= 0) {
          Toast('数量必须大于0')
          return
        }
        data.showDjModal = false
        let decimals = await data.windTokenContract.methods.decimals().call();
        let amount = new Bignumber(data.windAmount).multipliedBy(Math.pow(10, decimals)).toFixed();
         let balance = await data.windTokenContract.methods.decimals().call();
        if (amount > balance) {
          Toast('余额不足')
          return
        }
        let gasPrice = await data.web3.eth.getGasPrice()
        await data.freezeContract.methods.stake(amount).send({from: data.address, gasPrice: gasPrice});
         Toast('冻结成功')
        data.initData()
      },
      withdrawFreezeToken: async () => {
        if (Number(data.shAmount) <= 0) {
          Toast('数量必须大于0')
          return
        }
        if (Number(data.shAmount) <= Number(data.userBlance)) {
          data.showShModal = false;
          let decimals = await data.windTokenContract.methods.decimals().call();
          let amount = new Bignumber(data.shAmount).multipliedBy(Math.pow(10, decimals)).toFixed();
          let gasPrice = await data.web3.eth.getGasPrice()
          await data.freezeContract.methods.withdraw(amount).send({from: data.address, gasPrice: gasPrice});
           Toast('领取成功')
          data.initData()
        } else {
          Toast('数量不足')
        }
      },
      getFreezeIncome: async() => {
        let decimals = await data.BusdContract.methods.decimals().call();
        let income = await data.freezeContract.methods.earned(data.address).call();
        let decimalsWind = await data.windTokenContract.methods.decimals().call();
        let userBlance  = await data.freezeContract.methods.balanceOf(data.address).call();
        data.userBlance = new Bignumber(userBlance).dividedBy(Math.pow(10, decimalsWind)).toFixed(3);
        data.freezeIncome = new Bignumber(income).dividedBy(Math.pow(10, decimals)).toFixed(3);
      },
      getFreezeToken: async () => {
        let blance = await data.freezeContract.methods.totalSupply().call();
        let decimals = await data.windTokenContract.methods.decimals().call();
        console.log(blance)
        data.freezeTokenNum = Number(blance / Math.pow(10, decimals)).toFixed(2);
      },
      getMarketIncome: async () => {
        let curNum = await data.BsnDataContract.methods.dayNum().call()
        let decimals = await data.windTokenContract.methods.decimals().call();
        let result = await data.BsnDataContract.methods.getCurCycleData(curNum - 1).call()
        let userNum = await data.BsnDataContract.methods.getUserDayMarket(data.address, curNum -1).call();
        let isGet = await data.BsnDataContract.methods.isDividend(data.address, curNum -1).call();
        data.isGet = isGet
        if (!isGet) {
           data.marketIncome = new Bignumber(userNum).multipliedBy(result._windToken).dividedBy(result._marketToTalMoney).dividedBy(Math.pow(10, decimals)).toFixed(4)
        } else {
          data.marketIncome = 0.000
        }
        
      },
      getReward: async () => {
         let gasPrice = await data.web3.eth.getGasPrice()
        await data.freezeContract.methods.getReward().send({from: data.address, gasPrice: gasPrice});
        data.getFreezeIncome()
      }
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.pool-block {
  position: relative;
  overflow: hidden;
  &.pool-block--bg {
    background: #fff url('~@/assets/images/img2@2x.png') no-repeat right 0 !important;
    background-size: 87px auto !important;
  }
  .pool-block--title {
    font-size: 20px;
    line-height: 30px;
    color: #452A7A;
    font-weight: bold;
    text-align: center;
    margin-bottom: 15px;
  }
  .cell-balance {
    margin: 0 !important;
    .big-num {
      font-size: 25px !important;
      color: #452A7A !important;
    }
  }
  .fs12 {
    font-size: 12px !important;
  }
  .mt12 {
    margin-top: 12px !important;
  }
  .cell-button {
    margin-top: 14px !important;
    .van-button + .van-button {
      margin-left: 15px;
    }
  }
  .border-1px-t {
    margin-top: 20px !important;
    padding-top: 20px;
  }
  .pool-block--tip {
    margin-top: 12px;
    font-size: 12px;
  }
  .pool-block--link {
    font-size: 12px;
    line-height: 23px;
    .btn {
      padding: 0 17px;
      color: #fff;
      line-height: 23px;
      border-radius: 12px;
      font-size: 13px;
      background: #1FC7D4;
      margin-top: -12px;
    }
  }
  .icon-box {
    justify-content: flex-end;
    white-space: nowrap;
    .icon-right {
      height: 20px;
      margin-left: 5px;
    }
  }
  .cell-sellect {
    margin-bottom: 8px !important;
  }
}
.modal-tip {
  font-size: 14px;
  color: #8F80BA;
  margin-bottom: 10px;
}
.modal-box .modal-btn {
  margin-top: 30px !important;
}
</style>